.game-box{
    position: relative;
    min-height: 250px;
    max-height: 250px;
    border: solid 2px black;
    padding: 10px;
    overflow: hidden;
}
.hiden{
    display: none;
}
.game-started{
    opacity: 0.3;
}
.game-box__text.active{
    position: absolute;
    top: 10px;
    left: 10px;
}
.tab-box{
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1000px;
    margin: 0 auto;
}
.tab-last-best{
    display:flex;
    gap: 100px;
    text-wrap: nowrap;
}